/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}
.portfolio {
  text-align: center;
}
.project-display {
  margin-top: 30px;
}
.projects {
  margin: 20px 0;
}
.project-image {
  max-width: 80%;
  height: auto;
  border: 2px solid #ccc;
  margin-bottom: 10px;
}

.project-description {
  font-size: 16px;
}
/* Interests Component Styles */
.interests {
  text-align: center;
  padding: 20px;
}

.interest-items {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  flex-wrap: wrap;  /* Allow items to wrap on smaller screens */
}

.interest {
  padding: 10px;
  background-color: #8c8b8b;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  flex: 1 1 150px; /* Allow each item to take up space evenly */
  margin: 10px;
  text-align: center;
}

.interest:hover {
  background-color: #02f10a;
}

/* Droppable Container styled as a TV screen */
.drop-container {
  width: 70%;
  margin: 30px auto;
  padding: 20px;
  background-color: #0ae907;
  border: 5px solid #333;
  border-radius: 20px;
  min-height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: linear-gradient(135deg, #444, #111);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.8);
}

/* TV screen effect */
.drop-container::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  right: -15px;
  bottom: -15px;
  background: radial-gradient(circle, rgba(255,255,255,0.3), rgba(0,0,0,0.5));
  border-radius: 20px;
  z-index: -1;
}

.drop-container .content {
  text-align: center;
}

.drop-container .content img {
  width: 80%;
  max-width: 400px;
  height: auto;
  margin-bottom: 20px;
}

.drop-container p {
  font-size: 1.2rem;
  color: #fff;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.7);
}

/* TV Buttons */
.tv-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  position: absolute;
  bottom: 10px;
  width: 100%;
}

.tv-button {
  width: 30px;
  height: 30px;
  background-color: #ccc;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s;
}

.tv-button:hover {
  background-color: #888;
}

/* On/Off Button Effect */
.tv-button-on {
  background-color: #4caf50;
}

.tv-button-off {
  background-color: #f44336;
}

/* Responsive Styles */

/* On small screens, adjust the interest items */
@media (max-width: 768px) {
  .interest-items {
    flex-direction: column;  /* Stack interest items vertically */
    align-items: center; /* Center the items */
  }

  .drop-container {
    width: 90%; /* Make the drop container more narrow on smaller screens */
    height: auto; /* Allow the height to adjust automatically */
    min-height: 200px; /* Set a smaller minimum height */
  }

  .drop-container .content img {
    width: 100%; /* Make the image take full width on smaller screens */
    max-width: none; /* Remove the maximum width constraint */
  }

  .tv-buttons {
    bottom: 5px; /* Lower the position of the buttons on small screens */
  }

  .tv-button {
    width: 25px; /* Make buttons smaller on smaller screens */
    height: 25px;
  }
}

/* For even smaller screens (phones), adjust font size and layout */
@media (max-width: 480px) {
  .drop-container p {
    font-size: 1rem; /* Reduce text size on small screens */
  }

  .tv-buttons {
    bottom: 3px; /* Lower the buttons more */
  }

  .tv-button {
    width: 20px; /* Further reduce button size */
    height: 20px;
  }
}

