/* Fade in/out effect for the content when switching between pills */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.9);
  }
}

/* Apply the animations when content is toggled */
.red-pill-content.fadeIn,
.blue-pill-content.fadeIn {
  animation: fadeIn 0.5s ease-in-out forwards;
}

.red-pill-content.fadeOut,
.blue-pill-content.fadeOut {
  animation: fadeOut 0.5s ease-in-out forwards;
}

/* Glitch effect for page transition */
@keyframes glitch {
  0% { transform: translateX(0); text-shadow: 2px 0 red, -2px 0 blue; }
  20% { transform: translateX(-5px); text-shadow: 2px 0 red, -2px 0 blue; }
  40% { transform: translateX(5px); text-shadow: -2px 0 red, 2px 0 blue; }
  60% { transform: translateX(-5px); text-shadow: 2px 0 red, -2px 0 blue; }
  80% { transform: translateX(5px); text-shadow: -2px 0 red, 2px 0 blue; }
  100% { transform: translateX(0); text-shadow: 2px 0 red, -2px 0 blue; }
}

/* Apply the glitch effect */
.glitch {
  font-family: 'Courier New', Courier, monospace;
  position: relative;
  display: inline-block;
  animation: glitch 1s linear infinite;
  color: #fff;
}

.glitch:before, .glitch:after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  top: 0;
  animation: glitch 2s linear infinite;
  z-index: -1;
}

.glitch:before {
  color: rgb(38, 0, 255);
  clip: rect(24px, 9999px, 44px, 0);
}

.glitch:after {
  color: red;
  clip: rect(68px, 9999px, 88px, 0);
}

/* For glitch transition during page changes */
.page-glitch {
  animation: glitch 0.8s linear 1 forwards;
}

/* CSS for the morphing text effect */
@keyframes thresholdAnimation {
  0% {
    filter: blur(0);
    opacity: 1;
  }
  100% {
    filter: blur(8px);
    opacity: 0;
  }
}

#container {
