/* Tunnel effects for the background */
.red-tunnel, .blue-tunnel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  animation: tunnelEffect 5s infinite;
}

.red-tunnel {
  background: radial-gradient(circle, rgba(255, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
}

.blue-tunnel {
  background: radial-gradient(circle, rgba(0, 0, 255, 0.6), rgba(0, 0, 0, 0.6));
}

/* Tunnel effect animation */
@keyframes tunnelEffect {
  0% { transform: scale(1); opacity: 0.6; }
  50% { transform: scale(1.5); opacity: 0.3; }
  100% { transform: scale(1); opacity: 0.6; }
}

